<template>
    <div>
        <div id="screen-init"></div>
        <!-------------------- mobile start -------------------->
        <div v-if="mobile">
            <div class="header fixed">
                <div class="subject-box">{{subject}}</div>
                <div class="right-box">
                    <div class="layout-box" id="menu-response-box" @click="openResponse()">
                        <span>{{responseTitle}}</span> {{responseTimeInfo}} <span class="circle-box"><font-awesome-icon
                            icon="fas fa-square" style="color:#fff;font-size: 8px"></font-awesome-icon></span>
                    </div>
                    <span class="time">
                        <font-awesome-icon icon="far fa-clock" class="fa-clock"></font-awesome-icon> {{timeInfo}}
                    </span>
                    <div class="end-button-mobile">
                        <v-btn class="end-button" type="button" @click="userMeetingEnd()">끝내기</v-btn>
                    </div>
                </div>

            </div>
            <div class="menu" id="bottom-menu-box">
                <ul class="menu-box">
                    <li @click="loadComponent('poll')" v-if="IsPoll"><img src="@/assets/svg/menu/poll.svg"
                                                                          style="width:20px;"><span
                            class="title-over">설문조사</span> <span class="is-dot" v-if="IsPoll"></span></li>
                    <li @click="loadComponent('vote')" v-if="IsVote"><img src="@/assets/svg/menu/vote.svg"
                                                                          style="width:35px;padding-top:2px;"><span
                            class="title-over">투표</span> <span class="is-dot" v-if="IsVote"></span></li>
                    <li @click="loadComponent('data')"><img src="@/assets/svg/menu/data.svg"
                                                            style="width:35px;padding-top:2px;"><span
                            class="title-over">자료공유</span> <span class="is-dot" v-if="IsData"></span></li>
                    <li v-if="zoomBoardwriteView" @click="loadMobileSubMenu()">
                        <img src="@/assets/svg/menu/board.svg" style="width:20px;">
                        <span class="title-over posi-top">{{zoomBoardName}}</span> <span class="is-dot"
                                                                                         v-if="IsZoomBoard"></span>
                    </li>
                    <li @click="loadComponent('board')" v-if="!zoomBoardwriteView">
                        <img src="@/assets/svg/menu/board.svg" style="width:20px;">
                        <span class="title-over posi-top">{{zoomBoardName}}</span> <span class="is-dot"
                                                                                         v-if="IsZoomBoard"></span>
                    </li>

                </ul>
            </div>
            <v-dialog
                    v-model="subMenuShow"
                    width="250"

                    style="z-index:1000000000000;"
            >
                <div class="sub-menu-box">
                    <div class="sub-menu-header">
                        <div class="sub-menu-title">{{subMenuTitle}}</div>
                        <font-awesome-icon class="icon" icon="fa-solid fa-circle-xmark" @click="subMenuShow=false"/>
                    </div>
                    <ul class="sub-box">
                        <li class="sub-item" v-for="item in subMenuList" @click="loadComponent(item.code)">
                            {{item.name}}
                        </li>
                    </ul>
                </div>

            </v-dialog>
        </div>
        <!-------------------- mobile end -------------------->

        <div class="header fixed" v-else>
            <div class="logo" v-if="!logoUrl"><img src="@/assets/images/logo.png"></div>
            <div class="logo" v-if="logoUrl"><img :src="logoUrl"></div>
            <span class="subject-box"><span class="live-icon">LIVE</span> &nbsp; {{subject}}</span>
            <div class="menu">
                <ul class="menu-box">
                    <li @click="loadComponent('poll')" v-if="IsPoll"><img src="@/assets/svg/menu/poll.svg"
                                                                          style="width:20px;"><span
                            class="title-over">설문조사</span> <span class="is-dot" v-if="IsPoll"></span></li>
                    <li @click="loadComponent('vote')" v-if="IsVote"><img src="@/assets/svg/menu/vote.svg"
                                                                          style="width:35px;padding-top:2px;"><span
                            class="title-over">투표</span> <span class="is-dot" v-if="IsVote"></span></li>
                    <li @click="loadComponent('data')"><img src="@/assets/svg/menu/data.svg"
                                                            style="width:35px;padding-top:2px;"><span
                            class="title-over">자료공유</span> <span class="is-dot" v-if="IsData"></span></li>
                    <li v-if="zoomBoardwriteView">
                        <img src="@/assets/svg/menu/board.svg" style="width:20px;">
                        <span class="title-over posi-top">{{zoomBoardName}}</span>
                        <span class="is-dot" v-if="IsZoomBoard"></span>
                        <ul class="sub-box">
                            <li class="sub-item" @click="loadComponent('boardWrite')">글 작성</li>
                            <li class="sub-item" @click="loadComponent('board')">게시글 보기</li>
                        </ul>
                    </li>
                    <li @click="loadComponent('board')" v-if="!zoomBoardwriteView">
                        <img src="@/assets/svg/menu/board.svg" style="width:20px;">
                        <span class="title-over">{{zoomBoardName}}</span>
                        <span class="is-dot" v-if="IsZoomBoard"></span>
                    </li>

                </ul>
            </div>
            <div class="right-box">
                <ul class="right-ui">
                    <li class="response-info">
                        <div class="layout-box" id="menu-response-box" @click="openResponse()">
                            <span>{{responseTitle}}</span> {{responseTimeInfo}} <span class="circle-box"><font-awesome-icon
                                icon="fas fa-square" style="color:#fff;font-size: 8px"></font-awesome-icon></span>
                        </div>
                    </li>
                    <li class="time-info"><span><i class="far fa-clock"></i> 방송시간 </span> {{timeInfo}}</li>
                    <!--li class="badRequest-box">
                        <img src="@/assets/svg/btn_notify.svg" @click="badMeeting()">
                    </li-->
                    <li>
                        <v-btn class="end-button" type="button" @click="userMeetingEnd()">끝내기</v-btn>
                    </li>
                </ul>
            </div>
        </div>

        <div id="app-response" class="screenLayout">
            <pollRun :recepiveData="sendPollData" v-if="loadPollShow" @sendDataEvent="childSendData"></pollRun>
            <voteRun :recepiveData="sendVoteData" v-if="loadVoteShow" @sendDataEvent="childSendData"></voteRun>
            <examRun :recepiveData="sendExamData" v-if="loadExamShow" @sendDataEvent="childSendData"></examRun>
            <quizRun :recepiveData="sendQuizData" v-if="loadQuizShow" @sendDataEvent="childSendData"></quizRun>
        </div>

        <div class="screenLayout" v-if="screenShow">
            <examRun :recepiveData="sendExamResultData" :examResultCheck="'resultView'" v-if="loadResultExamShow"
                     @sendDataEvent="loadExamHide"></examRun>
            <dataView :gno="id" v-if="loadDataShow" @hideEvent="screenBoxHIde"></dataView>
            <liveFileView :no="fileNo" v-if="loadFileShow" @hideEvent="screenBoxHIde"></liveFileView>
            <div class="board-layout" v-if="zoomBoardUpdateShow || zoomBoardListShow || zoomBoardViewShow">
                <boardRegist :parentId="id" :no="updArticleNo" :userInfo="userInfo" :compontentType="'live'" v-if="zoomBoardUpdateShow"
                             @hideEvent="zoomBoardBoxHIde"></boardRegist>
                <boardList :parentId="id" :compontentType="'live'" v-if="zoomBoardListShow"
                           @articleView="showArticleView"
                           @hideEvent="zoomBoardBoxHIde"></boardList>
                <boardView :parentId="id" :no="articleNo" :compontentType="'live'" v-if="zoomBoardViewShow"
                           @articleUpdate="updateArticle" @hideEvent="zoomBoardBoxHIde"></boardView>
            </div>

            <div class="name-box rtq-box" v-if="badBoxShow">
                <div class="screen-header">
                    <div class="del-box" @click="hideBadBox()">
                        <font-awesome-icon style="color:#fff" icon="fas fa-times"></font-awesome-icon>
                    </div>
                </div>
                <form novalidate @submit.prevent="badSubmit">
                    <div class="summary"><span>'{{subject}}'</span><br> 신고 사유를 남겨주세요.</div>
                    <div class="field-box">
                        <v-textarea
                                outlined
                                placeholder="신고사유를 남겨주세요"
                                v-model="badContent"
                                :error-messages="badContentErrors"
                                @input="$v.badContent.$touch()"
                                @blur="$v.badContent.$touch()"
                                rows="4"

                        ></v-textarea>
                    </div>
                    <div class="button-box">
                        <v-btn class="submit-button" type="submit">보내기</v-btn>
                    </div>
                </form>
            </div>
            <div class="name-box exam-result-box" v-if="examResultShow">
                <div class="screen-header">
                    <div class="del-box" @click="hideExamResultBox()">
                        <font-awesome-icon style="color:#fff" icon="fas fa-times"></font-awesome-icon>
                    </div>
                </div>
                <div class="content-box">
                    <div class="title-box">제출이 완료되었습니다</div>
                    <div class="desc-box" v-if="ExmAnswer=='yes' && ExmDown!='yes'">
                        아래 답안보기 버튼을 눌러<br>시험 답안을 확인해 보세요
                    </div>
                    <div class="desc-box" v-if="ExmAnswer!='yes' && ExmDown=='yes'">
                        시험문제 다운로드를 원하는 경우<br>아래 다운로드 버튼을 눌러주세요
                    </div>
                    <div class="desc-box" v-if="ExmAnswer=='yes' && ExmDown =='yes'">
                        답안확인을 원하시면 답안보기 버튼을<br>
                        다운로드를 원하시면 다운로드 버튼을<br> 눌러주세요
                    </div>
                </div>
                <div :class="'button-row '+examDownButton">
                    <v-btn type="button" v-if="ExmAnswer=='yes'" @click="examGoodView()">답안보기</v-btn>
                    <v-btn type="button" v-if="ExmDown=='yes'" @click="examDownLoad()">다운로드</v-btn>
                    <v-btn type="button" class="cancle" @click="hideExamResultBox()">닫기</v-btn>
                </div>
            </div>
        </div>

    </div>

</template>


<script>


    import examView from '../../../video/setting/exam/examView'
    import pollRun from '../../../video/setting/poll/pollRun'
    import voteRun from '../../../video/setting/vote/voteRun'
    import examRun from '../../../video/setting/exam/examRun'
    import quizRun from '../../../video/setting/quiz/quizRun'
    import dataView from '../../../video/setting/data/dataView'
    import boardList from '../../../video/setting/board/boardList'
    import boardRegist from '../../../video/setting/board/boardRegist'
    import boardView from '../../../video/setting/board/boardview'
    import liveFileView from '../../../video/setting/data/liveFileView'


    import AUTH from '@/config/web/auth'
    import UTIL from '@/common/util'
    import JQ from 'jquery'
    import {
        API_URL_BROADCAST_GET_LOGO,
    } from "@/config/web/urls";
    //import {validationMixin} from 'vuelidate'
    //import {required} from 'vuelidate/lib/validators'

    import userLiveHandler from '../../handler/userLiveHandler';
    import userLiveComuHandler from '../../handler/userLiveComuHandler';


    export default {
        name: 'userVideo',
        components: {
            examView,
            pollRun,
            voteRun,
            examRun,
            quizRun,
            dataView,
            boardList,
            boardRegist,
            boardView,
            liveFileView
        },
        props: ["meetingInfo","joinStartActive"],
        /*
        mixins: [validationMixin],
        validations: {
            badContent: {required},

        },
        computed: {
            badContentErrors() {
                const errors = []
                if (!this.$v.badContent.$dirty) return errors
                if (!this.$v.badContent.required) {
                    errors.push('신고사유를 입력하세요')
                }
                return errors
            },
        },
        */
        watch: {
            joinStartActive(val) {
                if(val) {
                    this.startMeeting();
                }
            }
        },
        data() {
            return {
                DEBUG: ((process.env.NODE_ENV == 'development') ? true : false),
                mobile: this.$isMobile(),
                mobileClass: ((this.$isMobile()) ? 'mobile' : ''),
                uid: '',
                insertFlag: false,
                id: '',
                subject: '',
                meetingNumber: '',
                userName: '',
                userEmail: '',
                passWord: '',
                topMenuShow: false,
                screenShow: false,
                loadExamShow: false,
                loadDataShow: false,
                headerClass: '',
                loadPollShow: false,
                loadVoteShow: false,
                loadQuizShow: false,
                userReponseShow: false,
                userReponseParams: {},
                brdType: '',
                isAttendShow: false,
                fdataFrameStyle: {},
                status: '',
                noticeShow: false,
                sendParentParams: {},
                webinaDataShow: false,
                adminBadMessage: '',
                badScreenShow: false,
                zoomBoardName: '게시판',
                selectedArticleData: {},
                zoomBoardUpdateShow: false,
                zoomBoardListShow: false,
                zoomBoardwriteView: false,
                zoomBoardwriteAuth: '',
                zoomBoardViewShow: false,
                articleNo: '',
                updArticleNo: '',
                timeInterval: '',
                timeInfo: '00:00:00',

                IsData: false,
                IsPoll: false,
                IsVote: false,
                IsZoomBoard: false,
                responseTitle: '',
                responseTimeInfo: '00:00:00',
                timeResponseInterval: null,
                logoUrl: '',
                completeScreenShow: false,
                joinCnt: '',
                watchCnt: '',
                liveTime: '',
                goAfterFlag: false,
                noneCloseWin: true,
                badContent: '',
                badBoxShow: false,
                examResultShow: false,
                ExmAnswer: '',
                ExamDown: '',
                userPollComplete: false,
                sendPollData: {},
                sendVoteData: {},
                sendExamData: {},
                sendQuizData: {},
                isLogin: false,
                examResultNo: '',
                examNo: '',
                fileNo: '',
                loadFileShow: false,
                sendExamResultData: {},
                loadResultExamShow: false,
                bannerList: [],
                hostForceOutActive: false,
                hostEndActive: false,
                userInfo: {},
                subMenuShow: false,
                subMenuList: [],
                subMenuTitle: '',
            }
        },
        created() {

            AUTH.setDevice(this.$isMobile());
            let member = AUTH.getSession();
            member = null;
            if (member && member?.memberInfo) {
                this.uid = member.memberInfo.uid;
                this.userName = member.memberInfo.name;
                this.userEmail = member.memberInfo.email;

            } else {

                this.uid = sessionStorage.getItem('joinUid');
                this.userName = sessionStorage.getItem('userName');
                this.userEmail = sessionStorage.getItem('userEmail');

            }
            this.userInfo = {
                uid: this.uid,
                userName: this.userName,
                userEmail: this.userEmail
            }

            if (this.uid && this.userName) {
                this.$eventBus.$on('rpcJsonTransfer', this.transferAction);
                this.getMeetingInfo();

            } else {
                this.doError();
            }

        },
        methods: {
            /**** 방송 시작 부분 ****/
            getMeetingInfo() {
                if (!this.meetingInfo.info || !this.meetingInfo.info.id || this.meetingInfo.info.status == 'complete') {
                    alert('종료된 방송 입니다');
                    this.doError();
                    return;
                }

                this.id = this.meetingInfo.info.id;
                this.status = this.meetingInfo.info.status;
                this.subject = this.meetingInfo.info.subject;
                this.brdType = 'teacher';
                this.ExmAnswer = (this.meetingInfo.info.exam_imm_answer) ? this.meetingInfo.info.exam_imm_answer : 'yes';
                this.ExmDown = (this.meetingInfo.info.exam_down) ? this.meetingInfo.info.exam_down : 'no';
                this.logoUrl = (this.meetingInfo.info.logo) ? API_URL_BROADCAST_GET_LOGO + '?logo=' + this.meetingInfo.info.logo : '';

                if (this.meetingInfo.fileList) {
                    if (this.meetingInfo.fileList.length > 0) this.IsData = true;
                }
                this.IsPoll = (this.meetingInfo.pollInfo) ? true : false;
                this.IsVote = (this.meetingInfo.voteInfo) ? true : false;
                if (this.meetingInfo.userInfo?.pollInfo?.uid) {
                      this.userPollComplete = true;
                }
                if (this.meetingInfo.userInfo?.voteInfo?.uid) {
                    this.userVoteComplete = true;
                }

                if (this.meetingInfo.boardInfo) {
                    this.zoomBoardName = this.meetingInfo.boardInfo.boardName;
                    this.zoomBoardwriteAuth = this.meetingInfo.boardInfo.writeAuth;
                    this.IsZoomBoard = (this.meetingInfo.articleInfo) ? true : false;

                }
                switch (this.zoomBoardwriteAuth) {
                    case 'host':
                        this.zoomBoardwriteView = false;
                        break;
                    case 'user':
                        if (this.isLogin) {
                            this.zoomBoardwriteView = true;
                        } else this.zoomBoardwriteView = false;
                    case 'all':
                        this.zoomBoardwriteView = true;
                        break;
                }



            },
            startMeeting() {
                JQ('#screen-init').hide();
                let timeCnt = userLiveHandler.getStartTime();
                this.timeInterval = setInterval(() => {
                    this.timeInfo = userLiveHandler.setTimeVal(timeCnt);
                    timeCnt++;
                }, 1000);
            },

            /**** 방송 시작 부분 end ****/

            /**
             * @  저장된 정보 불러오기
             *
             */
            loadComponent(cmd) {
                switch (cmd) {
                    case 'poll':
                        if (this.userPollComplete) {
                            let message = {
                                message: '설문조사를 완료하였습니다',
                            };
                            this.$eventBus.$emit('modalOpen', message);
                            return;
                        }
                        break;
                    case 'vote':
                        if (this.userVoteComplete) {
                            let message = {
                                message: '투표를 완료하였습니다',

                            };
                            this.$eventBus.$emit('modalOpen', message);
                            return;
                        }
                        break;

                }
                const params = userLiveComuHandler.loadComponent(cmd)
                if (params.sendData && params.sendParamsId) {
                    this[params.sendParamsId] = params.sendData;
                }
                if (params.load) {
                    this[params.load] = true;
                }
                if (params.screenShow) this.screenShow = true;
                if (params.updArticleNo == 'new') this.updArticleNo = 'new';
                this.subMenuShow = false;

            },
            loadMobileSubMenu() {
                let boardMenus = [
                    {name: '글 작성', code: 'boardWrite'},
                    {name: '게시글 보기', code: 'board'},
                ]
                this.subMenuTitle = this.zoomBoardName;
                this.subMenuList = boardMenus;
                this.subMenuShow = true;
            },
            screenBoxHIde(event) {
                if (event.page == 'data') {
                    if (event.action == 'view') {
                        this.fileNo = event.data;
                        this.loadFileShow = true;
                    } else {
                        this.screenShow = false;
                    }
                    this.loadDataShow = false;
                } else if (event.page == 'fileView') {
                    this.screenShow = false;
                    this.loadFileShow = false;
                }

            },

            examGoodView() {
                this.sendExamResultData = {
                    resultNo: this.examResultNo

                };
                this.screenShow = true;
                this.examResultShow = false;
                this.loadResultExamShow = true;

            },
            hideExamResultBox() {
                this.screenShow = false;
                this.examResultShow = false

            },
            loadExamHide(event) {
                this.screenShow = false;
                this.loadResultExamShow = false;
            },
            /**
             * @ 호스트와 사용자간의 정보교환시 호스트의 정보를 받는 부분  handler
             *
             */
            transferAction(params) {
                switch (params.responseMethod) {
                    case 'userJoinPermit':
                        /*
                        this.$eventBus.$emit('overlay', 'hide');
                        if (params.cmd == 'yes') {
                            this.accessType = 'no';
                            userLiveHandler.updateUserStatusWithIng();
                            this.startMeeting();
                            return;
                        } else {
                            let message = {
                                message: '입장 거부 되었습니다',
                                doAction: () => {
                                    this.doError();
                                }
                            };
                            this.$eventBus.$emit('modalOpen', message);
                        }

                         */
                        break;
                    case 'insertPoll':
                        if (this.userPollComplete) return;
                        break;
                    case 'insertVote':
                        if (this.userVoteComplete) return;
                        break;
                    case 'hostForceOut':
                        let outUid = params.sendUid;
                        if (outUid != this.uid) return;
                        this.userOut();
                        break;
                    case 'forceResponseEnd':
                        this.examNo = 0;
                        this.loadExamShow = false;
                        this.sendVoteData = {};
                        this.loadVoteShow = false;
                        this.sendPollData = {};
                        this.loadPollShow = false;
                        this.loadQuizShow = false;
                        this.sendQuizData = {};

                        JQ('#app-response').hide();
                        JQ('#menu-response-box').hide();
                        if (this.timeResponseInterval) {
                            this.responseTimeInfo = '00:00:00'
                            clearInterval(this.timeResponseInterval);
                        }
                        return;
                        break;
                    case 'liveClose':
                        this.adminEndMeeting()
                        return;
                        break;
                    case  'insertFdata':
                        console.log(params,'-----------')
                        this.setFdata(params.data);
                        return;
                        break;

                }
                const resultParams = userLiveComuHandler.jsonRpcResponse(params);
                if (resultParams.sendParamsId && resultParams.sendData) {
                    this[resultParams.sendParamsId] = resultParams.sendData;
                }
                if (resultParams.load) {
                    this[resultParams.load] = true;
                }
                if (resultParams.examNo) {
                    this.examNo = resultParams.examNo;
                }
            },
            childSendData(event) {
                let rpcParams = {};
                JQ('#app-response').hide();

                switch (event.page) {
                    case 'barHide':
                        switch (event.active) {
                            case 'exam':
                                this.responseTitle = '시험 진행중';

                                break;
                            case 'poll':
                                this.responseTitle = '설문 진행중';
                                break;
                            case 'vote':
                                this.responseTitle = '투표 진행중';
                                break;
                            case 'quiz':
                                this.responseTitle = '간단퀴즈 진행중';
                                break;
                        }
                        if (this.timeResponseInterval) {
                            this.responseTimeInfo = '00:00:00'
                            clearInterval(this.timeResponseInterval);
                        }

                        let rtimeCnt = 0;
                        this.timeResponseInterval = setInterval(() => {
                            this.responseTimeInfo = userLiveHandler.setTimeVal(rtimeCnt);
                            rtimeCnt++;
                        }, 1000);
                        JQ('#menu-response-box').show();
                        break;

                    case 'exam':
                        switch (event.active) {
                            case 'replayExam':
                                userLiveComuHandler.sendReply('replayProblem');
                                break;
                        }
                        this.loadExamShow = false;
                        if (this.ExmAnswer == 'yes' || this.ExmDown == 'yes') {
                            this.screenShow = true
                            this.examResultShow = true;
                            this.examResultNo = this.examNo;
                            if (this.ExmAnswer == 'yes' && this.ExmDown == 'yes') {
                                this.examDownButton = 'triple';
                            }
                        }
                        this.examNo = 0;

                        break;
                    case 'poll':
                        switch (event.active) {
                            case 'replayPoll':
                                this.userPollComplete = true;
                                userLiveComuHandler.sendReply('replayPoll');
                                break;
                        }
                        this.sendPollData = {};
                        this.pollShow = false;
                        break;
                    case 'vote':
                        switch (event.active) {
                            case 'replayVote':
                                this.userVoteComplete = true;
                                userLiveComuHandler.sendReply('replayVote');
                                break;
                        }
                        this.sendVoteData = {};
                        this.voteShow = false;
                        break;
                    case 'quiz':
                        switch (event.active) {
                            case 'replayQuiz':
                                userLiveComuHandler.sendReply('replayQuiz');
                                break;
                        }
                        this.sendQuizData = {};
                        this.loadQuizShow = false;
                        break;

                }
            },
            /**
             * @ 호스트와 사용자간의 정보교환시 호스트의 정보를 받는 부분 handler End
             */

            /**
             * @ 게시판
             */
            zoomBoardBoxHIde(event) {
                //console.log(event)
                switch (event.page) {
                    case 'regist':
                        this.screenShow = false;
                        this.zoomBoardUpdateShow = false;
                        this.zoomBoardListShow = false;
                        this.zoomBoardViewShow = false;
                        break;
                    case 'list':
                        this.screenShow = false;
                        this.zoomBoardUpdateShow = false;
                        this.zoomBoardListShow = false;
                        this.zoomBoardViewShow = false;
                        break;
                    case 'view':
                        this.zoomBoardUpdateShow = false;
                        this.zoomBoardListShow = true;
                        this.zoomBoardViewShow = false;
                        break;
                }

            },
            showArticleView(no) {

                this.articleNo = no;
                this.zoomBoardUpdateShow = false;
                this.zoomBoardListShow = false;
                this.zoomBoardViewShow = true;
            },
            updateArticle(no) {
                this.updArticleNo = no;
                this.zoomBoardUpdateShow = true;
                this.zoomBoardListShow = false;
                this.zoomBoardViewShow = false;
            },
            /**
             * @ 게시판 end
             */
            goBannerUrl(url) {
                window.open(url);
            },
            openResponse() {
                JQ('#app-response').show();
                JQ('#menu-response-box').hide();
            },
            examDownLoad() {
                userLiveHandler.excelDown(this.examResultNo);

            },

            badMeeting() {
                this.screenShow = true;
                this.badBoxShow = true;


            },
            hideBadBox() {
                this.badContent = '';
                this.$v.$reset()
                this.screenShow = false;
                this.badBoxShow = false;

            },
            badSubmit() {
                this.$v.$touch()
                if (!this.$v.$anyError) {
                    this.hideBadBox();
                    userLiveComuHandler.badSubmit(this.badContent);
                }

            },

            setFdata(no) {
                this.screenShow = true;
                this.fileNo = no;
                this.loadFileShow = true;

            },

            /**
             * @  방송 종료
             */
            doError() {
                userLiveHandler.doEndMeetingActive();
            },
            userMeetingEnd() {
                let message = {
                    type: 'confirm',
                    message: '방송시청을 종료하시겠습니까?',
                    doAction: () => {
                        this.userMeetingEndActive();
                    }
                };
                this.$eventBus.$emit('modalOpen', message);

            },
            userMeetingEndActive() {
                userLiveHandler.doEndMeetingActive();


            },

            adminEndMeeting() {
                let message = {
                    message: '방송이 종료되었습니다',
                    doAction: () => {
                        userLiveHandler.doEndMeetingActive();
                    }
                };
                this.$eventBus.$emit('modalOpen', message);
            },
            userOut() {
                let message = {
                    message: '강제 퇴장 당했습니다',
                    doAction: () => {
                        userLiveHandler.doEndMeetingActive();
                    }
                };
                this.$eventBus.$emit('modalOpen', message);

            },
            /**
             * @  방송 종료 end
             */


        },
        mounted() {
            this.elementMounted = true;
            JQ(window).on("beforeunload", (eventObject) => {
                this.$emit('outActive',true)
                userLiveHandler.beforeUnload();
                return false;
            });


        },
        beforeDestroy() {

            this.$eventBus.$off('rpcJsonTransfer');
            if (this.timeInterval) {
                clearInterval(this.timeInterval)
            }
            if (this.timeResponseInterval) {
                clearInterval(this.timeResponseInterval)
            }
        }

    }
</script>
<style lang="scss" scoped="true">
    #app-response {
        display: none;
    }
</style>
